// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';

.eureka-heading, %eureka-heading {
    @include eureka-heading-font-shared;

    &--h1 {
        @include eureka-h1;
    }

    &--h2 {
        @include eureka-h2;
    }

    &--h3 {
        @include eureka-h3;
    }

    &--h4 {
        @include eureka-h4;
    }

    &--h5,
    &--h6 {
        @include eureka-h5;
    }
}
