// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-banner, %eureka-banner {
    color: var(--eureka-banner-color-text, eureka-light-theme('banner-color-text'));
    background-color: var(--eureka-banner-color-bg, eureka-light-theme('banner-color-bg'));
    box-sizing: border-box;
    border-radius: var(--eureka-banner-border-radius, eureka-light-theme('banner-border-radius'));
    $this: '.eureka-banner';

    &__inner {
        @include eureka-flex-row;
        box-sizing: border-box;
    }

    &__icon-container {
        min-width: #{eureka-to-px(eureka-light-theme('core-system-scale') * 5)};
        text-align: center;
        flex: 0 0 auto;
    }

    &__text-container {
        flex: 1 1 auto;
        margin: auto 0;
    }

    &__icon-container,
    &__text-container {
        box-sizing: border-box;
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 3)};
    }

    &__icon-container + &__text-container {
        padding-left: 0;
    }

    &__icon {
        color: var(--eureka-banner-color-icon, eureka-light-theme('banner-color-icon'));
        font-size: #{eureka-to-px(eureka-light-theme('core-system-scale') * 5)};
        display: block;
        margin: 0 auto;
    }

    #{$this}__title {
        @include eureka-h5;
        color: inherit;
        font-weight: bold;
        margin: 0;
    }

    &__msg-container-inner {
        @include eureka-sub-text;

        > :first-child {
            margin-top: 0;
        }

        > :last-child {
            margin-bottom: 0;
        }
    }

    &__title,
    &__msg-container-inner {
        @include eureka-max-line-width;
    }

    .eureka-hyperlink,
    .eureka-link-button {
        color: inherit;
    }

    .eureka-hyperlink,
    .eureka-link-button__text {
        text-decoration: underline;
    }

    // Mods
    &--affirmative {
        color: var(--eureka-banner-affirmative-color-text, eureka-light-theme('banner-affirmative-color-text'));
        background-color: var(--eureka-banner-affirmative-color-bg, eureka-light-theme('banner-affirmative-color-bg'));
    }

    &--affirmative &__icon {
        color: var(--eureka-banner-affirmative-color-icon, eureka-light-theme('banner-affirmative-color-icon'));
    }

    &--error {
        color: var(--eureka-banner-error-color-text, eureka-light-theme('banner-error-color-text'));
        background-color: var(--eureka-banner-error-color-bg, eureka-light-theme('banner-error-color-bg'));
    }

    &--error &__icon {
        color: var(--eureka-banner-error-color-icon, eureka-light-theme('banner-error-color-icon'));
    }

    &--info {
        color: var(--eureka-banner-info-color-text, eureka-light-theme('banner-info-color-text'));
        background-color: var(--eureka-banner-info-color-bg, eureka-light-theme('banner-info-color-bg'));
    }

    &--info &__icon {
        color: var(--eureka-banner-info-color-icon, eureka-light-theme('banner-info-color-icon'));
    }

    &--edge-to-edge {
        border-radius: 0;

        #{$this}__inner {
            padding-left: #{eureka-to-rem(eureka-light-theme('core-system-scale'))};
            padding-right: #{eureka-to-rem(eureka-light-theme('core-system-scale'))};
        }
    }
}
