// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';

.eureka-aria-accordion-item-trigger, %eureka-aria-accordion-item-trigger {
    .eureka-accordion-item-button {
        @include eureka-flex-row;
    }

    &__text-container {
        min-width: 1px;
        margin: auto 0;
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 4)} #{eureka-to-rem(eureka-light-theme('core-system-scale') * 5)};
        flex: 1 1 auto;
    }

    &__icon-container {
        @include eureka-flex-column;
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 4)};
        flex: 0 0 auto;
    }

    &__icon {
        color: var(--eureka-nav-action-icon-color, eureka-light-theme('nav-action-icon-color'));
        font-size: 2.8rem;
        margin: auto;
        flex: 0 0 auto;
    }

    &__text-container,
    &__icon-container {
        box-sizing: border-box;
    }

    .eureka-icon-chevron-down {
        transition: transform 0.25s ease;
    }

    &--is-expanded {
        .eureka-icon-chevron-down {
            transform: scaleY(-1);
        }
    }
}
