// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/resets.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/states.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-resource-list-item, %eureka-resource-list-item {
    display: block;
    $this: '.eureka-resource-list-item';

    .eureka-list-item__before-container + .eureka-list-item__text-container {
        padding-left: 0;
    }

    .eureka-list-item__after-container {
        @include eureka-flex-row;

        > * {
            margin: auto;
        }
    }

    &__after-icon-container {
        padding-left: 0;
    }

    &__icon-container {
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 3)};
        vertical-align: middle;
    }

    &--interactive {
        @include eureka-button-reset;
        @include eureka-focus-state-halo;
        @include eureka-hover-state;
        @include eureka-disabled-state;
        width: 100%;
        // Adding color: inherit to fix bug with iOS 15+
        // that changes color to blue.
        color: inherit;
        border: none;

        // stylelint-disable-next-line no-descending-specificity
        .eureka-list-item__text-container {
            text-align: left;
        }

        @media (hover: hover) {
            &:enabled:hover .eureka-list-item {
                background-color: var(--eureka-core-color-universal-hover-bg, #{eureka-light-theme('core-color-universal-hover-bg')});
            }
        }

        &:enabled:active .eureka-list-item {
            box-shadow: inset 0 0 3px rgba(0, 0, 0, 25%);
        }
    }
}
