@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/animations.scss';

.eureka-aria-accordion, %eureka-aria-accordion {
    .eureka-accordion-item-panel {
        display: block;
        animation: eureka-fade-in 0.35s ease-in;

        &[hidden] {
            display: none;
            opacity: 0;
        }
    }
}
