// ========================================================
// Dependencies
// ========================================================
@import 'react-toastify/dist/ReactToastify.css';
@import '~@Green-Dot-Corporation//eureka-lib-style-utils/src/styles/mixins/effects.scss';
@import '~@Green-Dot-Corporation//eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation//eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation//eureka-lib-theme-utils/src/styles/mixins/states.scss';
@import '~@Green-Dot-Corporation//eureka-lib-theme-utils/src/styles/mixins/typography.scss';
@import '~@Green-Dot-Corporation//eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-toaster, %eureka-toaster {
    @include eureka-box-shadow-elevated;
    min-height: 40px;
    color: var(--eureka-core-color-text, eureka-light-theme('core-color-text'));
    background-color: var(--eureka-core-color-canvas, eureka-light-theme('core-color-canvas'));
    border-radius: var(--eureka-toaster-border-radius, eureka-light-theme('toaster-border-radius'));
    padding: 0;
    $this: '.eureka-toaster';

    // stylelint-disable-next-line
    .Toastify__toast-body {
        padding: 0;
        margin: 0;
    }

    [class*='progress-bar'] {
        background: var(--eureka-toaster-progress-bar-color, eureka-light-theme('toaster-progress-bar-color'));
    }

    &__close-btn {
        @include eureka-focus-state-halo;
        color: inherit;
        background-color: transparent;
        border-radius: var(--eureka-toaster-border-radius, eureka-light-theme('toaster-border-radius'));
        align-self: flex-start;

        .eureka-button__content-container {
            padding: #{eureka-to-rem(eureka-light-theme('core-system-scale'))};
        }

        .eureka-button__icon {
            font-size: 1.4rem;
        }
    }

    &__content-container {
        @include eureka-flex-row;
        padding: 0 #{eureka-to-rem(eureka-light-theme('core-system-scale') * 2)};
    }

    &__icon-container {
        @include eureka-flex-row;
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 2)};
        flex: 0 0 auto;
    }

    &__icon-safe-space {
        width: 1em;
        height: 1em;
        font-size: 2.4rem;
        margin: 0 auto;
    }

    &__icon {
        font-size: inherit;
    }

    &__icon-container,
    &__icon-safe-space,
    &__icon {
        line-height: 0;
    }

    &__msg-container {
        @include eureka-flex-row;
        min-width: 1px;
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 2)};
        flex: 1 1 auto;
    }

    &__msg-container-inner {
        margin: auto 0;
    }

    &__title {
        @include eureka-h5;
    }

    &__msg {
        @include eureka-max-line-width;
        line-height: var(--eureka-core-typography-line-height, eureka-light-theme('core-typography-line-height'));
        margin: 0;
    }

    &__title + &__msg {
        margin-top: #{eureka-to-rem(eureka-light-theme('core-system-scale'))};
    }

    &--error {
        #{$this}__icon {
            color: var(--eureka-toaster-error-color-icon, eureka-light-theme('toaster-error-color-icon'));
        }
    }

    &--info {
        #{$this}__icon {
            color: var(--eureka-toaster-info-color-icon, eureka-light-theme('toaster-info-color-icon'));
        }
    }

    &--success {
        #{$this}__icon {
            color: var(--eureka-toaster-success-color-icon, eureka-light-theme('toaster-success-color-icon'));
        }
    }

    &--warning {
        #{$this}__icon {
            color: var(--eureka-toaster-warning-color-icon, eureka-light-theme('toaster-warning-color-icon'));
        }
    }
}
