// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/resets.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/states.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';
@import './mixins.scss';

.eureka-link-button, %eureka-link-button {
    @include eureka-button-reset;
    @include eureka-focus-state-halo;
    @include eureka-hover-state;
    color: var(--eureka-link-color-text, eureka-light-theme('link-color-text'));
    background-color: transparent;
    box-sizing: border-box;
    border-radius: #{eureka-to-px(eureka-light-theme('core-system-scale'))};
    $this: '.eureka-link-button';

    &__text {
        text-decoration: var(--eureka-link-text-decoration, eureka-light-theme('link-text-decoration'));
    }

    @media (hover: hover) {
        &:hover #{$this}__text {
            @include eureka-link-button-hover;
        }
    }

    &:focus-visible {
        @include eureka-link-button-focus-visible;
    }

    &__content-container {
        display: inline-block;
    }

    &__text-container {
        line-height: var(--eureka-core-typography-line-height, eureka-light-theme('core-typography-line-height'));
    }

    &__icon-container {
        vertical-align: middle;
    }

    &__text-container,
    &__icon-container {
        box-sizing: border-box;
        user-select: none;
        display: inline-block;
    }

    &__icon-container + &__text-container {
        vertical-align: middle;
        margin-left: eureka-to-px(eureka-light-theme('core-system-scale'));
    }

    &__icon-safe-space {
        @include eureka-flex-row;
        vertical-align: middle;
        box-sizing: border-box;
    }

    &__icon {
        @include eureka-flex-row;
        vertical-align: middle;
        margin: auto;
        flex: 0 0 auto;

        .eureka-icon {
            margin: auto;
        }
    }
}
