// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/effects.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/states.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-accordion-item-button, %eureka-accordion-item-button {
    @include eureka-focus-state-halo;
    color: var(--eureka-accordion-trigger-color-text, eureka-light-theme('accordion-trigger-color-text'));
    background-color: var(--eureka-accordion-color-bg, #{eureka-light-theme('accordion-color-bg')});

    @media (hover: hover) {
        &:hover {
            color: var(--eureka-accordion-trigger-hover-color-text, #{eureka-light-theme('accordion-trigger-hover-color-text')});
            background-color: var(--eureka-accordion-trigger-hover-color-bg, #{eureka-light-theme('accordion-trigger-hover-color-bg')});
            cursor: pointer;
        }
    }
}
